.media {
  background-color: #68C7F7;
  .youtube_video_card {
    margin-top: 24px;
    min-height: 500px;

    .video_wrapper {
      height: 330px;
    }

    .video_desc {
      padding-top: 15px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;

      .about_title {
        margin-top: 10px;
        font-size: 22px;
      }

      .date_btn {
        cursor: auto;
        margin-top: 10px;
        margin-right: 35px;
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 19px;
        color: #6d6a6a;
        &:hover {
          background-color: #fff;
        }

        svg {
          margin-right: 5px;
        }
      }
    }
  }
}
